import { isString } from '@dimjs/lang';

/**
 * @description
 * Parses a JSON string or object and returns the parsed result.
 *
 * @param json - The JSON string or object to parse.
 * @returns The parsed result.
 *
 * @docsCategory preference/utils/json
 * @codeFilter parse
 */
export const parse = <T = unknown>(json): T => {
  const result = isString(json) ? JSON.parse(json) : json;
  return result as T;
};
